
<template>

  <div id="parent" style="min-width: 100vh;min-height: 100vh;">
    <el-button :type="step1" id="btn_step1" class=" btn-arrow-right "></el-button>
    <span  class="  btn-link  "></span>
    <el-button  :type="step2"  id="btn_step2" class="btn-arrow-right "></el-button>
    <span  class="  btn-link  "></span>
    <el-button  :type="step3"  id="btn_step3" class="btn  btn-arrow-right"></el-button>
    <el-button id="btn_next" @click="next"></el-button>
    <el-button id="btn_prev" @click="prev"></el-button>
    <div id="container">
      <div id="p1" class="obj" @click="changeOptions" style="padding: 10px;left:50px;position:fixed;"></div>
      <div id="p2" class="obj" style="padding: 10px;left:350px;position:fixed;"></div>
      <div id="start"  >start</div>
      <div id="end"  >end</div>
    </div>
  </div>



</template>

<script>

import LeaderLine from "../../../../static/lib/leader-line.min";
import PlainDraggable from "../../../../static/lib/plain-draggable.min.js";
//var line ;
export default {
name: "StepLineExample",
  data(){
    return{
      line:null,
      draggable1:null,
      draggable2:null,

      current:0,
      step1:"info",
      step2:"info",
      step3:"info",
    }
  },
  mounted() {
      this.getLine();
  },
  destroyed() {
    this.line.remove();
    this.draggable1.remove();
    this.draggable2.remove();
  },
  methods:{
    changeOptions(){
      this.line.setOptions({'color':'green'});
    },
    getLine(){
      let startElement = document.getElementById('start');
      let endElement = document.getElementById('end');

      this.draggable1 = new PlainDraggable(document.getElementById('btn_next'));
      // this.draggable1 = new PlainDraggable(document.getElementById('btn_next') ,{
      //   autoScroll:document.getElementById('parent')
      // });
      //this.draggable1.containment = document.getElementById('container');
      this.draggable1.containment =  {left: 100, top: 100, width: 800, height: 400};;


      this.draggable2 = new PlainDraggable(document.getElementById('btn_prev'));

      // this.draggable2 = new PlainDraggable(document.getElementById('btn_prev') ,{
      //   autoScroll:document.getElementById('parent')
      // });
    //  this.draggable2.autoScroll = true;

      this.line = new LeaderLine(
        document.getElementById('p1'),
        document.getElementById('p2'),
        {
          startPlug: 'square',
          endPlug: 'crosshair',//'arrow1',//'hand',
        }
      );
      // line = new LeaderLine(startElement, endElement);
    },
    next(){
       this.current = this.current +1;
       if(this.current>3){
         this.current = 3;
         return;
       }
       if(this.current==1) {
         this.step1  = "success";
         this.step2  = "info";
         this.step3  = "info";
       }else if(this.current==2){
         this.step1  = "success";
         this.step2  = "success";
         this.step3  = "info";
       }else if(this.current==3){
         this.step1  = "success";
         this.step2  = "success";
         this.step3  = "success";
       }

    },
    prev(){
      this.current = this.current -1;
      if(this.current<0){
        this.current = 0;
        return;
      }
      if(this.current==0) {
        this.step1  = "info";
        this.step2  = "info";
        this.step3  = "info";
      }else if(this.current==1){
        this.step1  = "success";
        this.step2  = "info";
        this.step3  = "info";
      }else if(this.current==2){
        this.step1  = "success";
        this.step2  = "success";
        this.step3  = "info";
      }
    }
  }
}
</script>

<style scoped>
@import "../../../assets/css/bootstrap-directional-buttons.css";

#container- {
  display: inline-block;
  height: 500px;
  width: 500px;
  position: fixed;
}
#start {
  left: 0;
  top: 0;
  position: fixed;
}
#end {
  right: 0;
  bottom: 0;
  position: fixed;
}
.obj,#start,#end{

  /*position: fixed;*/
  position: fixed;
  width: 60px;
  height: 60px;
  background-color: red;
  color: #fff;
  line-height: 60px;
  text-align: center;
}

</style>



